import React, { useEffect, useState } from 'react';
import { useUserStore } from '../stores/userStore'
import Form from '../components/forms/Form';
import Sidebar from '../layout/sidebar/Sidebar';
import UserService from '../services/UserService';
import pattern from '../assets/patterns/backgroundpattern.svg';

function ProfilePage() {
    const { ci, email: emailFromStore } = useUserStore();
    const [email, setEmail] = useState(emailFromStore || '');
    const [id, setId] = useState(ci || '52384765');

    useEffect(() => {
        setEmail(emailFromStore);
        setId(ci);
    }, [emailFromStore, ci]);

    const inputs = [
        {
            id: 'email',
            label: 'Email',
            value: email,
            onChange: (e) => setEmail(e.target.value),
            type: 'email',
        },
        {
            id: 'id',
            label: 'Cdula',
            value: id,
            onChange: (e) => setId(e.target.value),
            type: 'text',
            disabled: true
        },
    ];

    const handleSubmit = () => {
        let response = UserService.changeEmail(ci, email);
    };

    return (
        <div className="overflow-x-auto my-2">
            <style>{`
                .pattern-bg {
                    background-image: url(${pattern});
                    background-repeat: repeat;
                    color: green;
                }
            `}</style>
            <div className="w-full flex">
                <Sidebar />
                <Form
                    onSubmit={handleSubmit}
                    buttonText="Guardar cambios"
                    titleText="Editar perfil"
                    inputs={inputs}
                />
                <div className="flex-grow opacity-50 text-green-100 pattern-bg"></div>
            </div>
        </div>
    );
}

export default ProfilePage;
