import React, { useState } from 'react';
import CustomModal from './CustomModal';
import InputField from '../inputs/InputField';
import InputLabel from '../inputs/InputLabel';
import Title from '../typography/Title';
import SubmitButton from '../buttons/SubmitButton';
import Text from '../typography/Text';

const UserEmailModal = ({ isOpen, onConfirm, gubEmail }) => {
    const [email, setEmail] = useState(gubEmail);
    const [errorMsg, setErrorMsg] = useState(null);

    const handleConfirm = () => {
        onConfirm(email);
    };

    return (
        <CustomModal isOpen={isOpen}>
            <form className="w-full space-y-4 flex flex-col px-8 font-sans">
                <Title text={"Configuracin de correo electrnico"} />
                <Text text={`El correo electrnico registrado en gub.uy es`} />
                <Text text={gubEmail} className="font-bold" />
                <Text text={`Sin embargo, puedes cambiarlo rellenando el siguiente campo:`} />
                <div className="flex space-x-4">
                    <div className="w-full">
                        <InputLabel htmlFor="email">Email</InputLabel>
                        <InputField
                            id="email"
                            type="email"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                        />
                    </div>
                </div>
                <div className="w-full flex justify-around pt-4">
                    <SubmitButton variant="notFinal" onClick={handleConfirm}>Confirmar email</SubmitButton>
                    <SubmitButton onClick={() => onConfirm(gubEmail)}>Continuar con email registrado</SubmitButton>
                </div>
            </form>
        </CustomModal>
    );
};

export default UserEmailModal;
