import create from 'zustand'

//TODO remover token en cuanto se tenga sistema de login
const getStateFromLocalStorage = () => {
    const token = localStorage.getItem('token');
    const email = localStorage.getItem('email');
    const ci = localStorage.getItem('ci');
    const id = localStorage.getItem('id');

    return { token, email, ci, id };
}

const saveStateToLocalStorage = (state) => {
    localStorage.setItem('token', state.token);
    localStorage.setItem('email', state.email);
    localStorage.setItem('ci', state.ci);
    localStorage.setItem('id', state.id);
}

export const useUserStore = create((set, get) => ({
    ...getStateFromLocalStorage(),
    updateUser: (token, email, ci, id) => {
        const newState = { token, email, ci, id };
        set(newState);
        saveStateToLocalStorage(newState);
    },
}));