import React, { useState, useEffect } from 'react';
import CustomModal from './CustomModal';
import InputField from '../inputs/InputField';
import InputLabel from '../inputs/InputLabel';
import Title from '../typography/Title';
import SubmitButton from '../buttons/SubmitButton';
import { ReactComponent as CloseIcon } from '../../assets/icons/close.svg';
import StepWizard from 'react-step-wizard';
import VehicleService from '../../services/VehicleService.js';
import PermissionService from '../../services/PermissionService';
import VtiForm from '../forms/VtiForm';
import Tags from '../Tags';
import AutoComplete from '../inputs/AutoComplete';


const Step1 = ({ setPlate, setBrand, setModel, setWeight, setWeightCapacity, plate, brand, model, weight, weightCapacity, ...props }) => {
    return (
        <div className="space-y-4 p-0 flex-1 flex flex-col justify-between h-rem-28 w-full">
            <div>
                <div className="flex space-x-4 mb-4">
                    <div className="flex-1">
                        <InputLabel htmlFor="plate">Patente</InputLabel>
                        <InputField id="plate" type="text" onChange={(e) => setPlate(e.target.value)} value={plate} />
                    </div>

                    <div className="flex-1">
                        <InputLabel htmlFor="brand">Marca</InputLabel>
                        <InputField id="brand" type="text" onChange={(e) => setBrand(e.target.value)} value={brand} />
                    </div>
                </div>

                <div className="mb-4">
                    <InputLabel htmlFor="model">Modelo</InputLabel>
                    <InputField id="model" type="text" onChange={(e) => setModel(e.target.value)} value={model} />
                </div>

                <div className="mb-4">
                    <InputLabel htmlFor="weight">Peso (Kg)</InputLabel>
                    <InputField id="weight" type="number" onChange={(e) => setWeight(e.target.value)} value={weight} />
                </div>
                <div className="mb-4">
                    <InputLabel htmlFor="weightCapacity">Capacidad de carga (Kg)</InputLabel>
                    <InputField id="weightCapacity" type="number" onChange={(e) => setWeightCapacity(e.target.value)} value={weightCapacity} />
                </div>
            </div>

            <div className="w-full flex justify-end items-end mt-auto pt-2 pb-8">
                <SubmitButton variant="notFinal" onClick={props.nextStep}>Siguiente</SubmitButton>
            </div>
        </div>
    );
};


const Step2 = ({
    vtis,
    setVtis,
    autocompleteOptions,
    handleRemovePermission,
    selectedPermissions,
    handleAddPermission,
    handleSubmit,
    ...props
}) => {
    return (
        <div className="space-y-4 p-0 flex flex-col justify-between h-rem-28 w-full">
            <div className="w-full h-full flex flex-col overflow-auto space-x-4 p-0">
                <div className="mb-4">
                    <div className="mb-4">
                        <VtiForm vtis={vtis} setVtis={setVtis} />
                    </div>
                    <div className="flex flex-col mb-4">
                        <InputLabel htmlFor="vti">Permisos</InputLabel>
                        <AutoComplete
                            options={autocompleteOptions}
                            onOptionSelect={handleAddPermission}
                        />
                    </div>
                    <Tags tags={selectedPermissions.map(permission => permission.label)} onTagRemove={handleRemovePermission} />
                </div>


            </div>

            <div className="w-full flex justify-between items-end mt-auto pt-2 pb-6">
                <SubmitButton variant="notFinal" onClick={props.previousStep}>
                    Anterior
          </SubmitButton>
                <SubmitButton onClick={handleSubmit}>Guardar datos</SubmitButton>
            </div>
        </div>
    );
};


const VehicleModal = ({ isOpen, onClose, selectedVehicle }) => {
    const [plate, setPlate] = useState('');
    const [brand, setBrand] = useState('');
    const [model, setModel] = useState('');
    const [weight, setWeight] = useState(null);
    const [weightCapacity, setWeightCapacity] = useState(null);
    const [vtis, setVtis] = useState([{ revisionDate: '', expirationDate: '' }]);
    const [permissions, setPermissions] = useState([]);
    const [autocompleteOptions, setAutocompleteOptions] = useState([]);
    const [selectedPermissions, setSelectedPermissions] = useState([]);

    useEffect(() => {

        if (selectedVehicle) {
            setPlate(selectedVehicle.plate);
            setBrand(selectedVehicle.brand);
            setModel(selectedVehicle.model);
            setWeight(selectedVehicle.weight);
            setWeightCapacity(selectedVehicle.weightCapacity);
            setVtis(selectedVehicle.vtis || [{ revisionDate: '', expirationDate: '' }]);
            setSelectedPermissions(selectedVehicle.permissions || []);

            const fetchWithSelectedVehicle = async () => {
                const fullPermisionInfo = await VehicleService.getVehicle(selectedVehicle.id);
                const permissionsFromApi = await PermissionService.getPermissions();

                setVtis(fullPermisionInfo.vtis.map(vti => ({ revisionDate: vti.revisionDate, expirationDate: vti.expirationDate })));

                let vehiclePermissiosId = fullPermisionInfo.permissions.map(permission => permission.permissionId);

                setSelectedPermissions(
                    permissionsFromApi
                        .filter(permissionFromApi => (vehiclePermissiosId.includes(permissionFromApi.id)))
                        .map(p => ({ id: p.id, label: p.name })));

                let mappedPermissionFromApi = permissionsFromApi.map(permission => ({
                    id: permission.id,
                    label: permission.name,
                }));

                setPermissions(mappedPermissionFromApi);

                setAutocompleteOptions(mappedPermissionFromApi.filter(option => vehiclePermissiosId.includes(option.id)));
            }

            fetchWithSelectedVehicle();
        } else {
            const fetchPermissions = async () => {
                const permissionsFromApi = await PermissionService.getPermissions();
                const mappedPermissions = permissionsFromApi.map(permission => ({
                    id: permission.id,
                    label: permission.name,
                }));
                setPermissions(mappedPermissions);
                setAutocompleteOptions(mappedPermissions);
            };
            fetchPermissions();
        }
    }, [selectedVehicle, isOpen]);

    const handleClose = () => {
        setPlate(null);
        setBrand(null);
        setModel(null);
        setWeight(null);
        setWeightCapacity(null);
        setVtis([{ revisionDate: '', expirationDate: '' }]);
        setSelectedPermissions([]);
        setAutocompleteOptions([]);
        setPermissions([]);
        onClose();
    }

    const handleSubmit = async () => {
        const vehicleData = {
            plate,
            brand,
            model,
            weight,
            weightCapacity,
            vtis,
            permissions: selectedPermissions
        };

        if (selectedVehicle) {
            vehicleData.id = selectedVehicle.id;
            await VehicleService.updateVehicle(vehicleData);
        } else {
            await VehicleService.createVehicle(vehicleData);
        }
        handleClose();
    }

    const handleRemovePermission = (label) => {
        setSelectedPermissions(selectedPermissions.filter(selectedPermission => selectedPermission.label !== label));
        let permission = permissions.find(p => p.label === label);
        setAutocompleteOptions([...autocompleteOptions, permission]);
    };

    const handleAddPermission = (permission) => {
        setAutocompleteOptions(autocompleteOptions.filter(option => option.label !== permission.label));
        setSelectedPermissions([...selectedPermissions, permission]);
    }

    return (
        <CustomModal isOpen={isOpen}>
            <div className="h-rem-35">
                <div className="absolute w-6 top-6 right-6 cursor-pointer text-black">
                    <CloseIcon onClick={handleClose} className="text-gray-600 hover:text-gray-800" />
                </div>
                <form className="w-full space-y-4 flex flex-col px-8 font-sans">
                    <Title text={selectedVehicle ? "Modificar vehiculo" : "Crear vehiculo"} />
                    <StepWizard transitions={{}} className="h-full flex-1">
                        <Step1 setPlate={setPlate} setBrand={setBrand} setModel={setModel} setWeight={setWeight} setWeightCapacity={setWeightCapacity} plate={plate} brand={brand} model={model} weight={weight} weightCapacity={weightCapacity} />
                        <Step2
                            vtis={vtis}
                            setVtis={setVtis}
                            handleSubmit={handleSubmit}
                            autocompleteOptions={autocompleteOptions}
                            handleAddPermission={handleAddPermission}
                            handleRemovePermission={handleRemovePermission}
                            selectedPermissions={selectedPermissions}
                        />
                    </StepWizard>
                </form>
            </div>
        </CustomModal>
    );
};

export default VehicleModal;

