import React, { useState, useEffect } from 'react';
import InputField from './InputField';

const AutoComplete = ({ options, onOptionSelect }) => {
  const [inputValue, setInputValue] = useState('');
  const [filteredOptions, setFilteredOptions] = useState([]);
  const [isFocused, setIsFocused] = useState(false);

  useEffect(() => {
    if (inputValue) {
      setFilteredOptions(
        options.filter(option =>
          option.label.toLowerCase().includes(inputValue.toLowerCase())
        )
      );
    } else {
      setFilteredOptions(options);
    }
  }, [inputValue, options]);

  const handleChange = e => {
    setInputValue(e.target.value);
  };

  const handleOptionClick = option => {
    setInputValue(option.label);
    setIsFocused(false);
    setFilteredOptions([]);
    onOptionSelect(option);
    setInputValue(null);
  };

  return (
    <div className="relative">
      <InputField
        id="auto-complete"
        type="text"
        value={inputValue}
        onChange={handleChange}
        onFocus={() => setIsFocused(true)}
        onBlur={() => setIsFocused(false)}
      />
      {isFocused && filteredOptions.length > 0 && (
        <div
          className="absolute z-10 mt-2 w-full bg-white border border-gray-200 rounded-md shadow-lg"
          onMouseDown={e => e.preventDefault()} // Prevents the onBlur event
        >
          <ul className="py-1">
            {filteredOptions.map((option, index) => (
              <li
                key={index}
                className="px-4 py-2 cursor-pointer hover:bg-gray-100"
                onMouseDown={() => handleOptionClick(option)} // Use onMouseDown instead of onClick
              >
                {option.label}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default AutoComplete;
