import axios from 'axios';

const apiClient = axios.create({
    baseURL: 'http://localhost:8090/carga-uy-core-1.0-SNAPSHOT/api',
    headers: {
        'Content-Type': 'application/json',
    },
});

class CompanyService {

    static async getCompanies({ size = 10, page = 1 }) {
        try {
            const response = await apiClient.get(`/companies?offset=${(page - 1) * size}&size=${size}`);
            return response.data;
        } catch (error) {
            console.error(`Error getting companies: ${error}`);
            throw error;
        }
    }

    static async getCompany(id) {
        try {
            const response = await apiClient.get(`/companies/${id}`);
            return response.data;
        } catch (error) {
            console.error(`Error getting company with id ${id}: ${error}`);
            throw error;
        }
    }

    static async createCompany({ rut, fantasyName, realName, mainAddress, areas }) {
        let mappedAreas = areas.map(area => {
            return { id: area.value };
        });

        let body = { rut, fantasyName, realName, mainAddress, areas: mappedAreas };
        try {
            const response = await apiClient.post('/companies', body);
            console.log("Empresa creada!");
            console.log("Datos: ", body);
            return response.data;
        } catch (error) {
            console.error(`Error creating company: ${error}`);
            throw error;
        }
    }

    static async updateCompany(id, { rut, fantasyName, realName, mainAddress, areas }) {
        let mappedAreas = areas.map(area => {
            return { id: area.value };
        });
        let body = { id, rut, fantasyName, realName, mainAddress, areas: mappedAreas };
        try {
            const response = await apiClient.put(`/companies/${id}`, body);
            console.log("Empresa modificada!");
            console.log("Datos: ", body);
            return response.data;
        } catch (error) {
            console.error(`Error updating company: ${error}`);
            throw error;
        }
    }

    static async deleteCompany(id) {
        try {
            const response = await apiClient.delete('/companies/' + id);
            console.log("Empresa eliminada!");
            return response.data;
        } catch (error) {
            console.error(`Error deleting company: ${error}`);
            throw error;
        }
    }
}

export default CompanyService;
