import './index.css';
import React from 'react';
import { BrowserRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import HomePage from './pages/HomePage';
import LoginPage from './pages/LoginPage';
import CompaniesPage from './pages/CompaniesPage';
import VehiclesPage from './pages/VehiclesPage';
import ProfilePage from './pages/ProfilePage';
import CompanyProfilePage from './pages/CompanyProfilePage';
import PageHeader from './layout/PageHeader';
import Footer from './layout/Footer';

function MainContent() {
  const location = useLocation();

  return (
    <main className="scroll-m-0">
      {location.pathname !== '/login' && <PageHeader />}
      <Routes>
        <Route path="/login" exact element={<LoginPage />} />
        <Route path="/" exact element={<HomePage />} />
        <Route path="/companies" exact element={<CompaniesPage />} />
        <Route path="/vehicles" exact element={<VehiclesPage />} />
        <Route path="/profile" exact element={<ProfilePage />} />
        <Route path="/company-profile" exact element={<CompanyProfilePage />} />
      </Routes>
    </main>
  );
}


function App() {
  return (
    <Router>
      <MainContent />
    </Router>
  );
}


export default App;
