import React from 'react';
import InputLabel from '../inputs/InputLabel';
import InputField from '../inputs/InputField';
import SubmitButton from '../buttons/SubmitButton';

const Form = ({ onSubmit, buttonText, titleText, inputs }) => {

  const renderComponent = (input) => {
    if (input.component) {
      return input.component;
    }
    else {
      return (<InputField
        id={input.id}
        value={input.value}
        onChange={input.onChange}
        type={input.type}
        disabled={input.disabled}
        className="block w-full px-4 py-2 mt-2 text-gray-700 bg-white border border-gray-200 rounded-md dark:bg-gray-800 dark:text-gray-300 dark:border-gray-600 focus:border-blue-400 focus:ring-blue-300 focus:ring-opacity-40 dark:focus:border-blue-300 focus:outline-none focus:ring"
      />);
    }
  }
  return (
    <section className="w-full max-w-2xl p-6 bg-white rounded-md shadow-md">
      <h2 className="text-lg font-semibold text-gray-700 capitalize dark:text-white">
        {titleText}
      </h2>

      <form>
        <div className="flex flex-col gap-6 mt-4">
          {inputs.map((input, index) => (
            <div key={index} className="w-full flex flex-col max-w-lg">
              <InputLabel htmlFor={input.id}>{input.label}</InputLabel>
              {renderComponent(input)}
            </div>
          ))}
        </div>
        <div className="flex justify-end mt-6 mr-24">
          <SubmitButton onClick={onSubmit}>{buttonText}</SubmitButton>
        </div>
      </form>
    </section>
  );
};

export default Form;
