import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';

const apiClient = axios.create({
    baseURL: 'http://localhost:8090/carga-uy-core-1.0-SNAPSHOT/api',
    headers: {
        'Content-Type': 'application/json',
    },
});

class VehicleService {

    static async getVehicles({ size = 10, page = 1 }) {
        try {
            const response = await apiClient.get(`/vehicles?size=${size}&offset=${(page - 1) * size}`);
            return response.data;
        } catch (error) {
            console.error(`Error getting vehicles: ${error}`);
            throw error;
        }
    }

    static async getVehicle(id) {
        try {
            const response = await apiClient.get(`/vehicles/${id}`);
            return response.data;
        } catch (error) {
            console.error(`Error getting vehicle with id ${id}: ${error}`);
            throw error;
        }
    }

    static async createVehicle({ plate, brand, model, weight, weightCapacity, vtis, permissions }) {
        let mappedPermissions = permissions.map(permission => {
            return { permissionId: permission.id };
        });

        let uuid = uuidv4();

        let body = { plate, brand, model, weight, weightCapacity: weightCapacity, vtis, permissions: mappedPermissions, gps: { uid: uuid } };
        body["companyId"] = 1;

        try {
            const response = await apiClient.post('/vehicles', body);
            console.log("Vehicle created!");
            console.log("Data: ", body);
            return response.data;
        } catch (error) {
            console.error(`Error creating vehicle: ${error}`);
            throw error;
        }
    }

    static async updateVehicle({ id, plate, brand, model, weight, weightCapacity, vtis, permissions }) {
        let mappedPermissions = permissions.map(permission => {
            return { id: permission.id };
        });
        let body = { id, plate, brand, model, weight, weightCapacity, vtis, permissions: mappedPermissions, gps: { uuid: plate } };
        body["company_id"] = 1;
        try {
            const response = await apiClient.put('/vehicles', body);
            console.log("Vehicle updated!");
            console.log("Data: ", body);
            return response.data;
        } catch (error) {
            console.error(`Error updating vehicle: ${error}`);
            throw error;
        }
    }

    static async deleteVehicle(id) {
        try {
            const response = await apiClient.delete('/vehicles/' + id);
            console.log("Vehicle deleted!");
            return response.data;
        } catch (error) {
            console.error(`Error deleting vehicle: ${error}`);
            throw error;
        }
    }
}

export default VehicleService;
