import React, { useEffect, useState } from 'react';
import CustomModal from './CustomModal';
import InputField from '../inputs/InputField';
import InputLabel from '../inputs/InputLabel';
import Title from '../typography/Title';
import SubmitButton from '../buttons/SubmitButton';
import AutoComplete from '../inputs/AutoComplete';
import { ReactComponent as CloseIcon } from '../../assets/icons/close.svg';
import Tags from '../Tags';
import CompanyService from '../../services/CompanyService'
import AreaService from '../../services/AreaService';

const CompanyModal = ({ isOpen, onClose, selectedCompany }) => {

  useEffect(() => {

    if (selectedCompany) {
      setRut(selectedCompany?.rut);
      setFantasyName(selectedCompany?.fantasyName);
      setRealName(selectedCompany?.realName);
      setMainAddress(selectedCompany?.mainAddress);
      setAreas(selectedCompany?.areas);
    } else {
      setRut('');
      setFantasyName('');
      setRealName('');
      setMainAddress('');
      setAreas([]);
    }


    const fetchAreas = async () => {
      const areas = await AreaService.getAreas();
      setOptions(areas.map(area => ({ label: area.name, value: area.id })));
    };

    fetchAreas();

  }, [selectedCompany]);

  const [options, setOptions] = useState(["toto", "totoo"]);
  const [rut, setRut] = useState('');
  const [fantasyName, setFantasyName] = useState('');
  const [realName, setRealName] = useState('');
  const [mainAddress, setMainAddress] = useState('');
  const [areas, setAreas] = useState([]);

  const removeArea = (areaLabelToRemove) => setAreas(areas.filter(area => area.label !== areaLabelToRemove));

  const handleSubmit = async () => {
    if (selectedCompany) {
      await CompanyService.updateCompany(selectedCompany.id, { id: selectedCompany.id, rut, fantasyName, realName, mainAddress, areas });
    } else {
      await CompanyService.createCompany({ rut, fantasyName, realName, mainAddress, areas });
    }

    onClose();
  }

  return (
    <CustomModal isOpen={isOpen}>
      <div className="absolute w-6 top-6 right-6 cursor-pointer text-black">
        <CloseIcon onClick={onClose} className="text-gray-600 hover:text-gray-800" />
      </div>
      <form className="w-full space-y-4 flex flex-col px-8 font-sans">
        <Title text={selectedCompany ? "Modificar empresa" : "Crear empresa"} />
        <div className="flex space-x-4">
          <div className="w-1/2">
            <InputLabel htmlFor="rut">RUT</InputLabel>
            <InputField
              id="rut"
              type="text"
              value={rut}
              onChange={(e) => setRut(e.target.value)}
            />
          </div>
          <div className="w-1/2">
            <InputLabel htmlFor="fantasyName">Nombre pblico</InputLabel>
            <InputField
              id="fantasyName"
              type="text"
              value={fantasyName}
              onChange={(e) => setFantasyName(e.target.value)}
            />
          </div>
        </div>
        <div className="flex space-x-4">
          <div className="w-1/2">
            <InputLabel htmlFor="realName">Nombre real</InputLabel>
            <InputField
              id="realName"
              type="text"
              value={realName}
              onChange={(e) => setRealName(e.target.value)}
            />
          </div>
          <div className="w-1/2">
            <InputLabel htmlFor="mainAddress">Direccin principal</InputLabel>
            <InputField
              id="mainAddress"
              type="text"
              value={mainAddress}
              onChange={(e) => setMainAddress(e.target.value)}
            />
          </div>
        </div>
        <div>
          <InputLabel htmlFor="areas">Rubros</InputLabel>
          <AutoComplete
            options={options}
            onOptionSelect={(option) => setAreas([...areas, option])}
          />
        </div>
        <div>
          <Tags tags={areas.map(area => area.label)} onTagRemove={removeArea} />
        </div>
        <div className="w-full flex justify-center pt-24 pb-8">
          <SubmitButton onClick={handleSubmit}>Guardar datos</ SubmitButton>
        </div>
      </form>
    </CustomModal>
  );
};

export default CompanyModal;
