import React from 'react';
import { ReactComponent as CloseIcon } from '../assets/icons/close.svg';

const Tag = ({ label, textColor, bgColor, onRemove }) => (
  <span
    className={`inline-flex items-center gap-1 rounded-full px-4 py-2 text-xs font-semibold ${textColor} ${bgColor}`}
  >
    {label}
    <CloseIcon onClick={onRemove} className="w-4 h-4 cursor-pointer" />
  </span>
);

const Tags = ({ tags, onTagRemove }) => {

  const handleTagRemove = (id) => {
    if (onTagRemove) {
      onTagRemove(id);
    }
  };

  return (
    <div className="flex gap-2">
      {tags.map((tag, index) => (
        <Tag
          key={index}
          label={tag}
          textColor={'text-black'}
          bgColor={'bg-soft-gray'}
          onRemove={e => handleTagRemove(tag)}
        />
      ))}
    </div>
  );
};

export default Tags;
