import axios from 'axios';

const apiClient = axios.create({
    baseURL: 'http://localhost:8090/carga-uy-core-1.0-SNAPSHOT/api',
    headers: {
        'Content-Type': 'application/json',
    },
});

class AreaService {

    static async getAreas() {
        try {
            const response = await apiClient.get('/areas');
            return response.data;
        } catch (error) {
            console.error(`Error getting areas: ${error}`);
            throw error;
        }
    }

    static async getArea(id) {
        try {
            const response = await apiClient.get(`/areas/${id}`);
            return response.data;
        } catch (error) {
            console.error(`Error getting area with id ${id}: ${error}`);
            throw error;
        }
    }

    static async createArea(area) {
        try {
            const response = await apiClient.post('/areas', area);
            console.log("Area creada!");
            console.log("Datos: ", area);
            return response.data;
        } catch (error) {
            console.error(`Error creating area: ${error}`);
            throw error;
        }
    }
}

export default AreaService;
