import React from 'react';
import { ReactComponent as LeftIcon } from '../assets/icons/left-arrow.svg';
import { ReactComponent as RightIcon } from '../assets/icons/right-arrow.svg';

const Pagination = ({ currentPage, totalPages, onPageChange }) => {
    const range = 2;

    const startPage = Math.max(2, currentPage - range);
    const endPage = Math.min(totalPages - 1, currentPage + range);

    const pages = [];
    for (let i = startPage; i <= endPage; i++) {
        pages.push(i);
    }

    const shouldShowStartEllipsis = startPage > 2;
    const shouldShowEndEllipsis = endPage < totalPages - 1;

    return (
        <div className="flex">
            <a
                onClick={currentPage === 1 ? null : () => onPageChange(currentPage - 1)}
                className={`flex items-center justify-center px-4 py-2 mx-1 text-gray-500 capitalize rounded-md cursor-${currentPage === 1 ? 'not-allowed' : 'pointer'} hover:bg-green hover:text-white`}
            >
                <LeftIcon />
            </a>

            <a
                onClick={() => onPageChange(1)}
                className={`px-4 py-2 mx-1 text-gray-700 transition-colors duration-300 transform rounded-md hover:bg-green hover:text-white ${1 === currentPage ? 'bg-green text-white' : 'bg-white'}`}
            >
                1
            </a>

            {shouldShowStartEllipsis && <span className="px-4 py-2 mx-1 text-gray-700 bg-white rounded-md">...</span>}

            {pages.map(page => (
                <a
                    key={page}
                    onClick={() => onPageChange(page)}
                    className={`px-4 py-2 mx-1 text-gray-700 transition-colors duration-300 transform rounded-md hover:bg-green hover:text-white ${page === currentPage ? 'bg-green text-white' : 'bg-white'}`}
                >
                    {page}
                </a>
            ))}

            {shouldShowEndEllipsis && <span className="px-4 py-2 mx-1 text-gray-700 bg-white rounded-md">...</span>}

            {totalPages !== 1 &&
                <a
                    onClick={() => onPageChange(totalPages)}
                    className={`px-4 py-2 mx-1 text-gray-700 transition-colors duration-300 transform rounded-md hover:bg-green hover:text-white ${totalPages === currentPage ? 'bg-green text-white' : 'bg-white'}`}
                >
                    {totalPages}
                </a>
            }

            <a
                onClick={currentPage === totalPages ? null : () => onPageChange(currentPage + 1)}
                className={`flex items-center justify-center px-4 py-2 mx-1 text-gray-700 transition-colors duration-300 transform rounded-md hover:bg-green hover:text-white hover:bg-green'}`}
            >
                <RightIcon />
            </a>
        </div>
    );
};

export default Pagination;
