import React, { useState, useEffect } from 'react';
import Table from '../components/tables/Table';
import Title from '../components/typography/Title';
import SearchBar from '../components/SearchBar';
import AddButton from '../components/buttons/AddButton';
import Sidebar from '../layout/sidebar/Sidebar';
import VehicleModal from '../components/modals/VehicleModal';
import VehicleService from '../services/VehicleService';
import Pagination from '../components/Pagination';

function VehiclesPage() {
    const headers = ["Matrcula", "Marca", "Modelo", "Peso del vehculo (Kg)", "Capacidad de carga (Kg)", "Acciones"];
    const [isOpen, setIsOpen] = useState(false);
    const [selectedVehicle, setSelectedVehicle] = useState(null);
    const [vehicles, setVehicles] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);

    useEffect(() => {
        const loadVehicles = async () => {
            try {
                const vehiclesFromApi = await VehicleService.getVehicles({ page: currentPage, size: 3 });
                setVehicles(vehiclesFromApi);
            } catch (error) {
                console.error(`Error fetching vehicles: ${error}`);
            }
        };
        loadVehicles();
    }, [isOpen, currentPage]);

    const data = vehicles.map(vehicle => ({
        fields: [vehicle.plate, vehicle.brand, vehicle.model, vehicle.weight, vehicle.weightCapacity],
        actions: {
            edit: () => {
                setSelectedVehicle(vehicle);
                setIsOpen(true);
            },
            delete: async () => {
                await VehicleService.deleteVehicle(vehicle.id);
                const vehiclesFromApi = await VehicleService.getVehicles();
                setVehicles(vehiclesFromApi);
            },
        },
    }));

    const handleClose = () => {
        setIsOpen(false);
        setSelectedVehicle(null);
    }

    return (
        <div className="overflow-x-auto my-2">
            <VehicleModal isOpen={isOpen} onClose={handleClose} selectedVehicle={selectedVehicle} />
            <div className="w-full flex">
                <Sidebar />
                <div className="w-5/6 min-h-screen bg-gray-200 flex items-start justify-center font-sans overflow-scroll">
                    <div className="w-full p-12 pt-0">
                        <Title text="Vehculos" />
                        <div className="flex justify-between items-start mb-6">
                            <div className="flex w-full items-center justify-between">
                                <SearchBar action={(value) => console.log(value)} />
                                <AddButton text={"Agregar vehculo"} action={() => setIsOpen(true)} />
                            </div>
                        </div>
                        <div className="w-full bg-white shadow-md rounded my-6">
                            <Table headers={headers} data={data} />
                        </div>
                        <div className="w-full flex justify-center">
                            <Pagination
                                totalPages={10}
                                currentPage={currentPage}
                                onPageChange={(page) => setCurrentPage(page)} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default VehiclesPage;
