import { format, parse, isValid, parseISO } from 'date-fns';
import InputField from '../inputs/InputField';
import InputLabel from '../inputs/InputLabel';

const VtiForm = ({ vtis, setVtis }) => {
    const handleRevisionDateChange = (index, e) => {
        const date = parseISO(e.target.value);
        const newVtis = [...vtis];
        newVtis[index] = {
            ...newVtis[index],
            revisionDate: isValid(date) ? format(date, 'dd/MM/yyyy') : ''
        };
        setVtis(newVtis);
    }

    const handleExpirationDateChange = (index, e) => {
        const date = parseISO(e.target.value);
        const newVtis = [...vtis];
        newVtis[index] = {
            ...newVtis[index],
            expirationDate: isValid(date) ? format(date, 'dd/MM/yyyy') : ''
        };
        setVtis(newVtis);
    }

    const formatDateForInput = (dateString) => {
        if (dateString) {
            const date = parse(dateString, 'dd/MM/yyyy', new Date());
            return format(date, 'yyyy-MM-dd');
        }
    }

    return (
        vtis.map((vti, index) => (
            <div className="flex space-x-4" key={index}>
                <div className="w-1/2">
                    <InputLabel htmlFor={`revisionDate_${index}`}>Fecha de revisin ITV</InputLabel>
                    <InputField
                        id={`revisionDate_${index}`}
                        type="date"
                        value={formatDateForInput(vti.revisionDate)}
                        onChange={(e) => handleRevisionDateChange(index, e)}
                    />
                </div>
                <div className="w-1/2">
                    <InputLabel htmlFor={`expirationDate_${index}`}>Fecha de expiracin ITV</InputLabel>
                    <InputField
                        id={`expirationDate_${index}`}
                        type="date"
                        value={formatDateForInput(vti.expirationDate)}
                        onChange={(e) => handleExpirationDateChange(index, e)}
                    />
                </div>
            </div>
        ))
    );
};

export default VtiForm;
