import React from 'react';
import Modal from 'react-modal';

// Es importante para mejorar la accesibilidad en screen readers
Modal.setAppElement('#root');

const CustomModal = ({ isOpen, children }) => {
  const closeModal = () => {
    // No hay funcin para cerrar el modal, ya que no hay botn
  };

  return (
    <div>
      <Modal
        isOpen={isOpen}
        onRequestClose={closeModal}
        contentLabel="Custom Modal"
        className="relative bg-white w-auto m-36 rounded-2xl min-h-[50%] min-w-[40%]"
        overlayClassName="fixed inset-0 bg-black bg-opacity-90 flex items-center justify-center"
      >
        {children}
      </Modal>
    </div>
  );
};

export default CustomModal;
