import { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";
import UserEmailModal from '../components/modals/UserEmailModal';
import AuthService from '../services/AuthService';
import { useUserStore } from '../stores/userStore';


const HomePage = () => {
  const navigate = useNavigate();

  const updateUser = useUserStore(state => state.updateUser);
  const email = useUserStore(state => state.email);

  const [isModalOpen, setIsModalOpen] = useState(false);

  useEffect(() => {
    const validateWithServer = async () => {
      const urlSearchParams = new URLSearchParams(window.location.search);
      const queryParams = Object.fromEntries(urlSearchParams.entries());
      const code = queryParams.code;
      if (code) {
        let authService = new AuthService();
        const response = await authService.validateWithServer(code);

        if (response && !response["already_exists"]) {
          updateUser(code, response.email, response["numero_documento"]);
          setIsModalOpen(true);
        } else {
          updateUser(code, response.email, response["numero_documento"]);
          navigate('/companies');
        }
      } else {
        navigate('/login');
      }
    }

    validateWithServer();
  }, [updateUser, navigate]);

  const handleEmailConfirm = async (email) => {
    setIsModalOpen(false);
    navigate('/companies');
  };

  return (
    <>
      <UserEmailModal isOpen={isModalOpen} onConfirm={handleEmailConfirm} gubEmail={email} />
    </>
  );
};

export default HomePage;
