import React from 'react';

const SubmitButton = ({ children, onClick, variant }) => {

  const handleClick = (e) => {
    e.preventDefault();
    onClick();
  };

  return (
    <button
      onClick={handleClick}
      className={`px-8 py-2.5 leading-5 text-white font-medium transition-colors duration-300 transform ${variant === 'notFinal' ? 'bg-indigo-500' : 'bg-green'} rounded-md hover:bg-gray-600 focus:outline-none focus:bg-gray-600`}
    >
      {children}
    </button>
  );
}

export default SubmitButton;
