import React from 'react';
import logo from '../../assets/images/truck-logo.svg';
import AuthService from '../../services/AuthService';

const LoginForm = () => {

    const handleLogin = () => {
        let authService = new AuthService();
        authService.tryToLogin();
    };

    return (
        <div className="rounded-xl bg-opacity-70 px-16 py-10 shadow-lg backdrop-blur-lg max-md:px-8">
            <div className="text-white">
                <div className="mb-8 flex flex-col items-center">
                    <img
                        src={logo}
                        width="150"
                        alt=""
                    />
                    <h1 className="mb-2 text-4xl">Carga UY</h1>
                    <span className="text-gray-100 text-xl ">Ingresa con tu usuario de gub.uy</span>
                </div>
                <div className="mt-8 flex justify-center text-lg text-black">
                    <button
                        type="submit"
                        className="rounded-3xl bg-green bg-opacity-50 px-10 py-2 text-white shadow-xl backdrop-blur-md transition-colors duration-300 hover:bg-green-600"
                        onClick={handleLogin}
                    >
                        Loguearse con usuario gub.uy
                    </button>
                </div>
            </div>
        </div>
    );
};

export default LoginForm;
