import React, { useEffect, useState } from 'react';
import Table from '../components/tables/Table';
import Title from '../components/typography/Title';
import SearchBar from '../components/SearchBar';
import AddButton from '../components/buttons/AddButton';
import Sidebar from '../layout/sidebar/Sidebar';
import CompanyModal from '../components/modals/CompanyModal';
import CompanyService from '../services/CompanyService';
import Pagination from '../components/Pagination';

function CompaniesPage() {
    const headers = ["Nmero de empresa", "Nombre pblico", "Razon social", "Direccin", "Acciones"];

    const [companies, setCompanies] = useState([]);
    const [isOpen, setIsOpen] = useState(false);
    const [selectedCompany, setSelectedCompany] = useState(null);
    const [currentPage, setCurrentPage] = useState(1);


    useEffect(() => {
        const fetchCompanies = async () => {
            try {
                const data = await CompanyService.getCompanies({ page: currentPage, size: 5 });
                setCompanies(data)
            } catch (error) {
                console.error(`Error fetching companies: ${error}`);
            }
        };

        fetchCompanies();
    }, [isOpen, currentPage]);

    const data = companies.map(company => ({
        fields: [company.rut, company.fantasyName, company.realName, company.mainAddress],
        actions: {
            edit: () => {
                setIsOpen(true);
                setSelectedCompany(company);
            },
            delete: async () => {
                await CompanyService.deleteCompany(company.id);
                const data = await CompanyService.getCompanies();
                setCompanies(data);
            },
        },
    }));

    const handleCloseModal = () => {
        setIsOpen(false);
        setSelectedCompany(null);
    };

    return (
        <>
            <CompanyModal isOpen={isOpen} onClose={handleCloseModal} selectedCompany={selectedCompany} />
            <div className="overflow-x-auto my-2">
                <div className="w-full flex">
                    <Sidebar />
                    <div className="w-5/6 min-h-screen bg-gray-200 flex items-start justify-center font-sans">
                        <div className="w-full p-12 pt-0">
                            <Title text={"Empresas"} />
                            <div className="flex justify-between items-start mb-6">
                                <div className="flex w-full items-center justify-between">
                                    <SearchBar action={(value) => console.log(value)} />
                                    <AddButton text={"Agregar empresa"} action={() => setIsOpen(true)} />
                                </div>
                            </div>
                            <div className="w-full bg-white shadow-md rounded my-6">
                                <Table headers={headers} data={data} />
                            </div>
                            <div className="w-full flex justify-center">
                                <Pagination
                                    totalPages={10}
                                    currentPage={currentPage}
                                    onPageChange={(page) => setCurrentPage(page)} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default CompaniesPage;
