import React, { useState } from 'react';
import Form from '../components/forms/Form';
import Sidebar from '../layout/sidebar/Sidebar';
import pattern from '../assets/patterns/backgroundpattern.svg';
import AutoComplete from '../components/inputs/AutoComplete';
import Tags from '../components/Tags';
import CompanyService from '../services/CompanyService';

function CompanyProfilePage() {
    const [rut, setRut] = useState('');
    const [fantasyName, setFantasyName] = useState('');
    const [realName, setRealName] = useState('');
    const [mainAddress, setMainAddress] = useState('');
    const [areas, setAreas] = useState(["koko"]);

    //MOCKS
    const options = ["lala", "lolo", "lele"];

    const removeArea = (areaId) => setAreas(areas.filter(area => areaId !== area));

    const handleSubmit = async (e) => {
        e.preventDefault();
        const data = {
            rut,
            fantasyName,
            realName,
            mainAddress,
            areas
        };
        await CompanyService.editCompany(data);
    };

    const inputs = [
        {
            id: 'rut',
            label: 'RUT',
            value: rut,
            onChange: (e) => setRut(e.target.value),
            type: 'text',
        },
        {
            id: 'fantasyName',
            label: 'Nombre Pblico',
            value: fantasyName,
            onChange: (e) => setFantasyName(e.target.value),
            type: 'text',
        },
        {
            id: 'realName',
            label: 'Nombre real',
            value: realName,
            onChange: (e) => setRealName(e.target.value),
            type: 'text',
        },
        {
            id: 'mainAddress',
            label: 'Direccin Principal',
            value: mainAddress,
            onChange: (e) => setMainAddress(e.target.value),
            type: 'text',
        },
        {
            id: 'areas',
            label: 'Rubros',
            component: (
                <div className="min-h-36">
                    <AutoComplete
                        options={options}
                        onOptionSelect={(option) => setAreas([...areas, option])}
                    />
                    <div className="my-8">
                        <Tags tags={areas} onTagRemove={removeArea} />
                    </div>
                </div>
            ),
        },
    ];

    return (
        <div className="overflow-x-auto my-2">
            <style>{`
        .pattern-bg {
          background-image: url(${pattern});
          background-repeat: repeat;
          color: green;
        }
      `}</style>
            <div className="w-full flex">
                <Sidebar />
                <Form
                    onSubmit={handleSubmit}
                    buttonText="Guardar cambios"
                    titleText="Editar empresa"
                    inputs={inputs}
                />
                <div className="flex-grow opacity-50 text-green-100 pattern-bg"></div>
            </div>
        </div>
    );
}

export default CompanyProfilePage;
