import React from 'react';

function AddButton({ text, action }) {
    return (
        <button className="bg-green bg-opacity-80 flex items-center text-white font-medium px-4 py-2 rounded-md ml-4 focus:outline-none hover:bg-opacity-100 transition-opacity"
            onClick={() => action()}>
            <i className="material-icons mr-2">add</i>
            {text ?? "agregar"}
        </button>
    );
}

export default AddButton;
